.card {
    height: 120px;
    margin: 10px, 10px, 10px, 10px;
    cursor: pointer;
    -webkit-transition: all .2s ease-in-out;
    -o-transition: all .2s ease-in-out;
    transition: all .2s ease-in-out;
    overflow: hidden;
    text-align: center;
    float: center
}

.card:hover {
   -webkit-transform: scale(1.06);
       -ms-transform: scale(1.06);
           transform: scale(1.06);
    }

img {
    width: 50%;
}



